import {
       mapState,  
       mapMutations  
   } from 'vuex'; 
import mpvueCityPicker from '@/components/mpvue-citypicker/mpvueCityPicker.vue'
export default {
	data() {
		return {
			loadModal: false,
			isShowEmpty: true,  // 是否展示不限
			form: {
				title: '',
				cotenant_type: 1,  // 1.预租房 2.合租房
				cotenant_description: ''
			},
			index: -1,
			picker: [
				{label: '2户合租', value: 2},
				{label: '3户合租', value: 3},
				{label: '4户合租', value: 4},
				{label: '5户合租', value: 5}
			],
			modalName: null,
			region:{label:"请点击选择地址",value:[],cityCode:""},
			themeColor: '#007AFF',
			cityPickerValue: [1, 1, 0],
			cityPickerLabel: [],
			errorInfo:{
				title: false,
				place: false,
				count: false
			}
		}
	},
	components: {
		mpvueCityPicker
	},
	onLaunch: function() {
		uni.getSystemInfo({
		        success: function(e) {









		            Vue.prototype.StatusBar = e.statusBarHeight;
		            let custom = wx.getMenuButtonBoundingClientRect();
		            Vue.prototype.Custom = custom;
		            Vue.prototype.CustomBar = custom.bottom + custom.top - e.statusBarHeight;





		        }
		    })
	},
	computed: {
	     ...mapState(['userInfo'])  
	},
	methods: {
		BackPage() {
			uni.navigateBack({
				delta: 1
			});
		},
		PickerChange(e) {
			this.index = e.detail.value
		},
		textareaBInput(e) {
			this.form.cotenant_description = e.detail.value
		},
		onCancel(e) {
			console.log(e)
		},
		chooseCity() {
			this.focus()
			this.$refs.mpvueCityPicker.show()
		},
		onConfirm(e) {
			this.region = e;
			let arr = e.label.split('-')
			this.cityPickerValue = e.value;
			this.cityPickerLabel = arr
		},
		focus () {
			this.errorInfo.title = false
			this.errorInfo.place = false
			this.errorInfo.count = false
		},
		formSubmitPre: function(e) {
			if (!this.form.title) {
				this.errorInfo.title = true
				return false
			}
			if (this.region.label == '请点击选择地址') {
				this.errorInfo.place = true
				return false
			}
			if (this.index < 0) {
				this.errorInfo.count = true
				return false
			}
			this.loadModal = true
			let params = {
				title: this.form.title,
				province: this.cityPickerLabel[0],
				city: this.cityPickerLabel[1],
				district: this.cityPickerLabel[2],
				cotenant_type: this.form.cotenant_type,
				cotenant_count: this.picker[this.index].value,
				cotenant_description: this.form.cotenant_description
			}
			this.$api.publishRoom(params).then(res => {
				this.loadModal = false
				uni.showToast({title: '发布成功',icon:"success"});
				setTimeout(() => {
					uni.switchTab({
					    url: '/pages/home/home',
					    success: function(e) {
					        uni.setStorage({
					            key: 'hometype',
					            data: '2'
					        });
					    }
					});
				}, 500)
			}).catch(err => {
				this.loadModal = false
			})
		}
	}
}
